/* CSS Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

html {
  color-scheme: dark light;
}

img,
picture,
svg,
video {
  display: block;
  max-width: 100%;
}

/* Custom CSS */
body {
  background: linear-gradient(#0ea5e9, #0369a1);
  position: relative;
  min-height: 100vh;
}

h1,
h2,
h3,
p,
span {
  font-family: "Montserrat", sans-serif;
  color: white;
}

img {
  filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(320deg) brightness(112%) contrast(99%);
}

.hide {
  display: none !important;
}

@media (max-width: 850px) {
  header {
    flex-direction: column;
    gap: 18px;
  }
}
@media (max-width: 700px) {
  main {
    gap: 30px !important;
  }
  .current-weather-section {
    width: 75% !important;
    gap: 10px !important;
  }
  .weather-body {
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1.8fr !important;
    gap: 30px !important;
  }
  .weather-main {
    flex-direction: row !important;
    justify-content: center;
  }
  .weather-details {
    gap: 20px !important;
  }
  .forecast-container {
    width: 90% !important;
    justify-content: none;
  }
  .day-container {
    grid-template-columns: 1fr 1fr !important;
    grid-template-rows: 1fr 1fr !important;
    justify-items: center;
    gap: 5px !important;
  }
  .day-main {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .min-temp {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
@media (max-width: 560px) {
  .weather-main {
    flex-direction: column !important;
    align-items: center;
    gap: 0px !important;
  }
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 35px 30px 20px 30px;
}

.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  text-decoration: none;
}
.banner h1 {
  font-size: 2em;
  font-weight: bold;
}
.banner img {
  height: 40px;
  width: 40px;
}

#search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 8px 14px;
  border-radius: 30px;
  background-color: rgba(0, 0, 0, 0.7);
  border: 2px solid transparent;
  transition: border-color 0.2s;
}
#search-form:focus-within {
  border-color: white !important;
}
#search-form:hover #clear-button, #search-form:focus-within #clear-button {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.2s;
}
#search-form input {
  width: 200px;
  background-color: transparent;
  border: none;
  outline: none;
  color: white;
  font-size: 1.2em;
  font-weight: bold;
}
#search-form button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}
#search-form #clear-button {
  opacity: 0;
  pointer-events: none;
}
#search-form #search-button img {
  height: 22px;
  width: 22px;
}

.units {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.units button {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 10px;
  border-radius: 30px;
  border: 2px solid transparent;
  transition: border-color 0.2s;
  cursor: pointer;
}
.units button:hover {
  border-color: white;
}

.active-unit {
  border-color: white !important;
  pointer-events: none;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}

.error-message {
  background-color: white;
  color: red;
  padding: 7px 17px;
  border-radius: 20px;
  font-weight: bold;
  font-size: 0.9em;
}

.current-weather-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  width: 700px;
  margin-top: 20px;
}

.weather-heading {
  text-align: center;
}
.weather-heading h2 {
  font-size: 2.3em;
  font-weight: bold;
}
.weather-heading p {
  font-size: 1.2em;
  font-weight: bold;
  color: rgb(211, 211, 211);
}

.weather-body {
  display: grid;
  gap: 50px;
  grid-template-columns: 1fr 2fr;
}

.weather-main {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.weather-main .weather-main-top {
  display: flex;
}
.weather-main .weather-main-top img {
  height: 90px;
  width: 90px;
}
.weather-main .weather-main-top p {
  font-size: 3.8em;
  font-weight: bold;
}
.weather-main .weather-main-bottom .condition-text {
  font-size: 1.4em;
  font-weight: bold;
}
.weather-main .weather-main-bottom .feels-like {
  font-size: 1.1em;
  font-weight: bold;
  color: rgb(211, 211, 211);
}

.weather-details {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
}
.weather-details div div {
  display: flex;
  position: relative;
}
.weather-details h4 {
  color: rgb(211, 211, 211);
  margin-bottom: 7px;
  font-size: 1.1em;
}
.weather-details p {
  font-size: 1.3em;
  font-weight: bold;
}

.forecast-container {
  display: grid;
  justify-content: center;
  margin-bottom: 100px;
}
.forecast-container .day-container:not(:last-child) {
  border-bottom: 2px solid white;
}

.forecast-title {
  text-align: center;
  font-size: 1.8em;
  font-weight: bold;
  margin-bottom: 20px;
}

.day-container {
  display: grid;
  gap: 30px;
  grid-template-columns: 1.2fr 1.2fr 1fr 1fr;
  align-items: center;
  font-size: 1.2em;
  font-weight: bold;
  padding: 14px 0px;
}
.day-container img {
  height: 30px;
  width: 30px;
}
.day-container .day-main,
.day-container .max-wind {
  display: flex;
  align-items: center;
  gap: 6px;
}
.day-container .min-temp {
  color: rgb(211, 211, 211);
}

.wind-degree {
  display: flex;
  align-items: center;
  justify-content: center;
}
.wind-degree svg {
  width: 23px;
  fill: white;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 30px;
  font-weight: bold;
}
footer p {
  color: rgb(211, 211, 211);
  text-align: center;
}
footer a {
  text-decoration: none;
  color: white;
  transition: color 0.2s;
}
footer a:hover {
  color: gray;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

#loading {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

#loading div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}

#loading div:nth-child(1) {
  animation-delay: -0.45s;
}

#loading div:nth-child(2) {
  animation-delay: -0.3s;
}

#loading div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
